/**
 * Links & buttons
 **/
a.link.main-link {
  text-decoration: underline;
  border-bottom: 1px solid #2E4A70; }

.btn {
  font-size: 2.3em;
  border-radius: 30px;
  border: 1px solid;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  padding: 14px 40px; }
  .btn.main-btn {
    color: #FAEEB9;
    background-color: #2E4A70;
    border-color: #2E4A70; }
    .btn.main-btn:hover, .btn.main-btn:focus, .btn.main-btn:active {
      color: #f7e289;
      background-color: #1f324c;
      border-color: #1f324c; }
  .btn.scnd-btn {
    color: #23385F;
    background-color: #FAEEB9;
    border-color: #FAEEB9; }
    .btn.scnd-btn:hover, .btn.scnd-btn:focus, .btn.scnd-btn:active {
      color: #15223a;
      background-color: #f7e289;
      border-color: #f7e289; }
  .btn.important {
    text-transform: uppercase; }

a.btn {
  font-size: 2.3em; }
